import { createContext, useState } from '@wordpress/element';

import { DEFAULT_DATA } from './data';
import type { DataShape } from './types';

export type DataState = DataShape & {
	isSubmitting?: boolean;
	result?: any;
};

export type UseDataState = [DataState, React.Dispatch<React.SetStateAction<DataState>>];

const StateContext = createContext<UseDataState>(null);

const { Provider, Consumer: StateConsumer } = StateContext;

export interface StateProviderProps {
	selectedPosts: DataState['posts'];
}

const StateProvider: React.FC<StateProviderProps> = ({ children, selectedPosts }) => {
	const useDataState = useState<DataState>(() => ({
		posts: selectedPosts,
		isSubmitting: false,
		...DEFAULT_DATA,
	}));

	return <Provider value={useDataState}>{children}</Provider>;
};

export { StateContext, StateProvider, StateConsumer };
