import { useCallback } from '@wordpress/element';
import { Button, Icon, ToggleControl } from '@wordpress/components';
import { chevronUp, chevronDown } from '@wordpress/icons';

import { __ } from '@wp-plugins/i18n';

import { useInstanceData, useUpdateInstanceField } from '../services';
/**
 * Parts of this component are copied from @wordpress/components
 */

export const PanelBodyTitle = ({ isOpened, title, id, isDisabled, instanceData, onToggle, ...props }) => {
	const data = useInstanceData(id) || instanceData;
	const updateField = useUpdateInstanceField();

	const onToggleActive = useCallback(
		(isChecked) => {
			updateField(id, 'active')(isChecked);

			// Close panel when deactivated
			if (!isChecked && isOpened) {
				onToggle();
			}
		},
		[id, isOpened, onToggle, updateField]
	);

	if (!title) return null;

	return (
		<h2 className='components-panel__body-title wptelegram_pro_instances__title'>
			<label className='wptelegram_pro_instances__title--label' aria-label={__('Use this instance')}>
				<ToggleControl
					className='wptelegram_pro_instances__title--toggle'
					checked={data.active}
					onChange={onToggleActive}
				/>
			</label>
			<Button
				className='components-panel__body-toggle'
				disabled={isDisabled || !data.active}
				aria-expanded={isOpened}
				onClick={onToggle}
				{...props}
			>
				<span aria-hidden='true'>
					<Icon className='components-panel__arrow' icon={isOpened ? chevronUp : chevronDown} />
				</span>
				{title}
			</Button>
		</h2>
	);
};
