import { useCallback } from '@wordpress/element';
import { Modal as WPModal } from '@wordpress/components';
import { __ } from '@wp-plugins/i18n';

import { Instances, Instance } from '@wp-plugins/wptelegram-shared--pro-gb-ip';

import { useDataState } from '../services';
import { Footer } from './Footer';
import { Settings } from './Settings';

export type ModalProps = {
	isOpen?: boolean;
	onClose?: VoidFunction;
};

export const Modal: React.FC<ModalProps> = ({ isOpen = true, onClose }) => {
	const [{ instances }, setState] = useDataState();

	const getInstance = useCallback((id) => instances[id], [instances]);
	const updateInstance = useCallback(
		<K extends keyof Instance>(id: number, field: K) => {
			return (value: Instance[K]) => {
				setState((prevState) => ({
					...prevState,
					instances: {
						...prevState.instances,
						[id]: {
							...prevState.instances?.[id],
							[field]: value,
						},
					},
				}));
			};
		},
		[setState]
	);

	return (
		<WPModal
			open={isOpen}
			className='wptelegram-pro-instant-post'
			onRequestClose={onClose}
			overlayClassName='wptelegram-pro-instant-post__overlay'
			shouldCloseOnEsc={false}
			shouldCloseOnClickOutside={false}
			title={__('Send to Telegram')}
		>
			<div className='wptelegram-pro-instant-post__content'>
				<div className='wptelegram-pro-instant-post__body'>
					<div className='wptelegram-pro-instant-post__body--instances'>
						<Instances getInstance={getInstance} updateInstance={updateInstance} />
					</div>
					<hr />
					<Settings />
				</div>
				<Footer onClose={onClose} />
			</div>
		</WPModal>
	);
};
