import { DataProvider, TDataContext } from './DataProvider';

export const withData = <P extends any>(Component: React.ComponentType<P>): React.ComponentType<P & TDataContext> => {
	const WrappedComponent: React.ComponentType<P & TDataContext> = (props) => {
		return (
			<DataProvider {...props}>
				<Component {...props} />
			</DataProvider>
		);
	};
	return WrappedComponent;
};
