import $ from 'jquery';
import ReactDOM from 'react-dom';
import { setLocaleData, __ } from '@wp-plugins/i18n';

import { App } from './App';
import { ROOT_ID } from '../constants';
import { StateProvider, DataShape } from '../services';

const root = document.getElementById(ROOT_ID);

setLocaleData(window['wptelegram_pro'].i18n, 'wptelegram-pro');

const postListForm = $('.wp-admin.edit-php .wrap form#posts-filter');

const unmountApp = () => {
	ReactDOM.unmountComponentAtNode(root);
};

const renderApp = (selectedPosts: DataShape['posts']) => {
	ReactDOM.render(
		<StateProvider selectedPosts={selectedPosts}>
			<App onClose={unmountApp} />
		</StateProvider>,
		root
	);
};

// register event handlers on document ready
$(() => {
	$('button.wptelegram-pro-p2tg-instant-post-btn').on('click', function (e) {
		e.preventDefault();

		const posts = postListForm.find<HTMLInputElement>('.wp-list-table input:checkbox[name="post[]"]:checked');

		const selectedPosts = Array.from(
			posts.map(function () {
				return Number(this.value);
			})
		);

		if (!selectedPosts.length) {
			alert(__('Please select a post to send.'));
		} else {
			renderApp(selectedPosts);
		}
	});
});
