import { useCallback } from '@wordpress/element';
import { BaseControl, CheckboxControl } from '@wordpress/components';

import { __ } from '@wp-plugins/i18n';

import { useUpdateInstanceField } from '../services';
import type { InstanceProps, UiData } from '../services';

const instances: UiData['instances'] = (window['wptelegram_pro'] as any)?.uiData?.instances || {};

export const Channels: React.FC<InstanceProps> = ({ id, isDisabled, data }) => {
	const updateField = useUpdateInstanceField();

	const onChange = useCallback(
		(channel) => () => {
			const hasChannel = data.channels.indexOf(channel) !== -1;
			const newChannels = hasChannel ? data.channels.filter((c) => c !== channel) : [...data.channels, channel];
			updateField(id, 'channels')(newChannels);
		},
		[data.channels, id, updateField]
	);

	return (
		<BaseControl id={`wptgpro-send-to-${id}-base`} label={__('Send to')}>
			<div role='group' id={`wptgpro-send-to-${id}`} aria-label={__('Send to')}>
				{instances[id]?.channels?.map((channel, index) => {
					return (
						<CheckboxControl
							key={channel + index}
							label={channel}
							disabled={isDisabled}
							checked={data.channels.indexOf(channel) !== -1}
							onChange={onChange(channel)}
						/>
					);
				})}
			</div>
		</BaseControl>
	);
};
