import type { Instance } from '@wp-plugins/wptelegram-shared--pro-gb-ip';
import type { DataShape, UiData } from './types';

const DEFAULT_INSTANCE_DATA: Instance = {
	active: true,
	channels: [],
	delay: '0',
	disable_notification: false,
	message_template: '',
	send_featured_image: true,
	files: {},
};

const allInstances: UiData['instances'] = (window['wptelegram_pro'] as any)?.uiData?.instances || {};

// Add default fields to instances
const defaultInstances = Object.entries(allInstances || {}).reduce(
	(instances, [id, instance]) => ({ ...instances, [id]: { ...DEFAULT_INSTANCE_DATA, ...instance } }),
	{}
);

export const DEFAULT_DATA: DataShape = {
	instances: defaultInstances,
	force_send: true,
	ignore_all_rules: false,
	ignore_delay: true,
};
