import { useCallback } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

import { useData, useDataState } from '../services';
import { __KEY__ } from './constants';

export const useOnSubmit = () => {
	const [dataState, setState] = useDataState();

	const { rest_namespace } = useData('api');

	const path = `${rest_namespace}/p2tg-instant-post/`;

	return useCallback(async () => {
		const { posts, instances, force_send, ignore_all_rules, ignore_delay } = dataState;

		let instanceEntries = Object.entries(instances).filter(([, instance]) => instance?.active);

		if (ignore_delay) {
			instanceEntries = instanceEntries.map(([id, instance]) => [id, { ...instance, delay: '0' }]);
		}

		const data = {
			posts,
			[__KEY__]: {
				override_switch: true,
				send2tg: true,
				instances: Object.fromEntries(instanceEntries),
				force_send,
				source: 'INSTANT_POST',
			},
			ignore_all_rules,
		};

		let result: any;

		try {
			setState((prevState) => ({ ...prevState, isSubmitting: true, result: null }));

			result = await apiFetch({ method: 'POST', path, data });
		} catch (error) {
			console.log('ERROR', error);
			result = 'ERROR';
		} finally {
			setState((prevState) => ({ ...prevState, isSubmitting: false, result }));
		}

		return result;
	}, [dataState, path, setState]);
};
