import { useCallback } from '@wordpress/element';
import { ToggleControl } from '@wordpress/components';

import { __, sprintf } from '@wp-plugins/i18n';

import { useDataState, DataShape } from '../services';

export const Settings: React.FC = () => {
	const [{ force_send, ignore_all_rules, ignore_delay }, setState] = useDataState();

	const onToggleField = useCallback(
		<K extends keyof DataShape>(field: K) =>
			(isChecked: boolean) => {
				setState((prevState) => ({
					...prevState,
					[field]: isChecked,
				}));
			},
		[setState]
	);

	return (
		<>
			<h4>{__('Settings')}</h4>
			<ToggleControl
				label={sprintf(/* translators: %s Rule name */ __('Ignore %s rules.'), __('Use when'))}
				checked={force_send}
				// @ts-ignore
				disabled={ignore_all_rules}
				onChange={onToggleField('force_send')}
			/>
			<ToggleControl
				label={__('Ignore all rules.')}
				checked={ignore_all_rules}
				onChange={onToggleField('ignore_all_rules')}
			/>
			<ToggleControl
				label={__('Send instantly without delay.')}
				checked={ignore_delay}
				onChange={onToggleField('ignore_delay')}
			/>
		</>
	);
};
