import { Button } from '@wordpress/components';
import { __ } from '@wp-plugins/i18n';
import { useOnSubmit, useDataState } from '../services';

export type FooterProps = {
	onClose: VoidFunction;
};

export const Footer: React.FC<FooterProps> = ({ onClose }) => {
	const onSubmit = useOnSubmit();
	const [{ isSubmitting, result }] = useDataState();

	return (
		<div className='wptelegram-pro-instant-post__footer'>
			{result ? (
				<div className='wptelegram-pro-instant-post__footer--result'>
					{__('Result:')}&nbsp;<b>{result === 'ERROR' ? __('There was some error.') : __('Done')}</b>
				</div>
			) : null}
			<div className='wptelegram-pro-instant-post__footer--buttons'>
				<Button isPrimary onClick={onSubmit} isBusy={isSubmitting} disabled={isSubmitting}>
					{__('Send')}
				</Button>
				<Button isSecondary onClick={onClose} disabled={isSubmitting}>
					{__('Cancel')}
				</Button>
			</div>
		</div>
	);
};
