import { createContext, useMemo } from '@wordpress/element';
import type { Instance } from './types';

export interface TDataContext {
	getInstance: (id: number) => Instance;
	updateInstance: <K extends keyof Instance>(id: number, field: K) => (value: Instance[K]) => void;
}

const DataContext = createContext<TDataContext>(null);

const { Provider, Consumer: DataConsumer } = DataContext;

const DataProvider: React.FC<TDataContext> = ({ children, getInstance, updateInstance }) => {
	const value = useMemo<TDataContext>(() => ({ getInstance, updateInstance }), [getInstance, updateInstance]);

	return <Provider value={value}>{children}</Provider>;
};

export { DataContext, DataProvider, DataConsumer };
