import { useState, useCallback } from '@wordpress/element';
import { Panel, PanelBody, Disabled } from '@wordpress/components';

import { __ } from '@wp-plugins/i18n';

import type { UiData } from '../services';
import { Instance } from './Instance';
import { withData } from '../services';
import { PanelBodyTitle } from './PanelBodyTitle';

import './styles.scss';

const allInstances: UiData['instances'] = (window['wptelegram_pro'] as any)?.uiData?.instances || {};

const instances = Object.entries(allInstances).sort(
	// sort first menu order then by id
	([id_a, a], [id_z, z]) => Number(a.menu_order) - Number(z.menu_order) || Number(id_z) - Number(id_a)
);

const hasSingleInstance = 1 === instances.length;

export interface InstancesProps {
	isDisabled?: boolean;
	noHeaderIfSingle?: boolean;
}

const Instances: React.FC<InstancesProps> = ({ isDisabled = false, noHeaderIfSingle = true }) => {
	const [opened, setOpened] = useState({});

	const onToggle = useCallback((id) => () => setOpened((v) => ({ ...v, [id]: !v[id] })), []);

	const output = instances.map(([id, { title, ...data }]) => {
		const instance = <Instance key={id} id={Number(id)} isDisabled={isDisabled} data={data} />;

		return hasSingleInstance && noHeaderIfSingle ? (
			instance
		) : (
			<PanelBody key={id} opened={opened[id]} initialOpen={false}>
				{({ opened: isOpened }) => (
					<>
						<PanelBodyTitle
							id={id}
							isDisabled={isDisabled}
							instanceData={data}
							isOpened={isOpened}
							title={title}
							onToggle={onToggle(id)}
						/>
						{isOpened && instance}
					</>
				)}
			</PanelBody>
		);
	});

	return (
		<Disabled
			// @ts-ignore
			isDisabled={isDisabled}
		>
			{hasSingleInstance ? (
				<>{output}</>
			) : (
				<>
					<h4>{__('Instances')}</h4>
					<Panel>{output}</Panel>
				</>
			)}
		</Disabled>
	);
};

export default withData(Instances);
