import type { CSSProperties } from 'react';
import { ToggleControl, TextControl, TextareaControl } from '@wordpress/components';

import { __ } from '@wp-plugins/i18n';

import { Channels } from './Channels';
import { Files } from './Files';
import { useInstanceData, useUpdateInstanceField } from '../services';
import type { InstanceProps } from '../services';

// eslint-disable-next-line react-perf/jsx-no-new-object-as-prop
const separatorStyles: CSSProperties = {
	height: '1.5em',
};

const separator = <div style={separatorStyles}></div>;

export const Instance: React.FC<InstanceProps> = ({ id, isDisabled, data: instanceData }) => {
	const data = useInstanceData(id) || instanceData;
	const updateField = useUpdateInstanceField();

	const disabled = isDisabled || !data.active;

	return (
		<>
			{!disabled && (
				<>
					{separator}
					<Channels id={id} data={data} />
					{separator}
					<Files id={id} data={data} isDisabled={disabled} />
					{separator}
					<ToggleControl
						label={__('Disable Notifications')}
						checked={data.disable_notification}
						onChange={updateField(id, 'disable_notification')}
					/>
					{separator}
					<TextControl
						label={__('Delay in Posting')}
						value={data.delay}
						onChange={updateField(id, 'delay')}
						step='0.5'
						min={0}
						type='number'
					/>
					{separator}
					<ToggleControl
						label={__('Featured Image')}
						checked={data.send_featured_image}
						onChange={updateField(id, 'send_featured_image')}
						help={__('Send Featured Image (if exists).')}
					/>
					{separator}
					<TextareaControl
						label={__('Message Template')}
						value={data.message_template}
						onChange={updateField(id, 'message_template')}
						rows={10}
					/>
				</>
			)}
		</>
	);
};
